<template>
  <group class="jq-mt">
    <popup-picker :title="dateTile" :data="timeList" :value.sync="time" ></popup-picker>
  </group>
  <group class="jq-mt">
    <div class="chart" v-ebar="barData"></div>
  </group>
  <group class="jq-mt">
    <div class="chart" v-ebar="barDataS"></div>
  </group>
</template>
<script>
  //import { Group,Cell,PopupPicker} from 'vux/src/components'
  import Cell from '*/cell'
  import PopupPicker from '*/popup-picker'
  import Group from '*/group'
  import { getList } from '../../../untils/api'

  export default {
    components: {
      PopupPicker,
      Group,
      Cell
    },
    data () {
      return {
        msg: '利润',
        dataOne:[],
        permissions:localStorage.getItem("permissions"),
        dateTile:'日期',
        time: formatData.defaultTime,
        timeList: formatData.dateList,
        barData:{},
        barDataS:{}
      }
    },
    computed: {

    },
    ready(){
     document.title = this.msg;
    },
    methods: {
      async getBarLineData() {
      let id = "";
      commit('UPDATE_LOADING', true)
      if(this.permissions=='202'){
        id = "APP10201c";
        }else if(this.permissions=='203'||this.permissions=='201'){
          if(this.permissions=='203'){
               id = "APP10201b";
          }else{
            id = "APP10201a";
          }
        }

     let date=formatData.dateConversion(this.time);
   let dataTit = await getList(id,{date_:date});
        if(dataTit.success){

          const TableList = dataTit.data;
          this.barData = {};
          this.barData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
            commit('UPDATE_LOADING', false)
        }else{
            commit('UPDATE_LOADING', false)
          console.log(dataTit.message);
        }
      },
      async getBarLineDataS() {
          commit('UPDATE_LOADING', true)
        let id = "";
        if(this.permissions=='202'){
        id = "APP10203c";
        }else if(this.permissions=='203'||this.permissions=='201'){
          if(this.permissions=='203'){
             id = "APP10203b";
          }else{
            id = "APP10203a";
          }
        }
     let date=formatData.dateConversion(this.time);
   let dataTit = await getList(id,{date_:date});
      console.log(dataTit);
        if(dataTit.success){
          const TableList = dataTit.data;
          this.barDataS = {};
          this.barDataS.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
            commit('UPDATE_LOADING', false)
        }else{
            commit('UPDATE_LOADING', false)
          console.log(dataTit.message);
        }
      }
    },
    watch: {
     time (newtime, oldtime) {
     this.getBarLineData();
      this.getBarLineDataS();
       formatData.setDateTime(newtime);
    }
  },
    created: function () {
      this.barData={};
      this.barData.dataset=[];
      this.getBarLineData();

      this.barDataS={};
      this.barDataS.dataset=[];
      this.getBarLineDataS();
    }
  }
</script>



